@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Raleway',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#comp-model {
  width: 100vw;
  height: 100vh;
}

#landing-main {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  background-color: #333;
}

#landing-head {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 5em;
  color: #57cbcc;
  text-align: center;
  margin: 5px;
  letter-spacing: -0.1em;
}

#landing-button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 10px;
}

#landing-button button {
  margin: 15px;
  padding: 10px;
  width: 120px;
  border: none;
  font-family: 'Josefin Sans', sans-serif;
  color: #57cbcc;
  border-radius: 50px;
  background: #333333;
  box-shadow: 2px 2px 6px #2b2b2b,
            -2px -2px 6px #3b3b3b;
}

#landing-instructions {
  background-color: rgba(87, 203, 204, 1);
  margin: 5px;
  width: 100%;
  text-align: center;
  padding: 5px 0px 5px 0px;
  color: #333;
}

#landing-instructions div{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#landing-instructions div p {
  margin: 2px;
}

#landing-instructions div svg {
  margin: 2px;
  fill: #333;
}

#landing-button button:hover {
  border: 1px solid #57cbcc;
}

#landing-toggle {
  display: none;
  position: absolute;
  z-index: 5;
  padding: 10px;
  margin: 10px;
}

@media screen and (max-width: 600px) {
  #landing-head {
    font-size: 3.5em;
  }

  #landing-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
  }

  #landing-button button {
    width: 200px;
  }
}

@media screen and (max-width: 400px) {
  #landing-head {
    font-size: 2.75em;
  }
  #landing-instructions div p {
    font-size: small;
  }
  #landing-instructions div svg {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}
